.ContentHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  overflow: hidden;
}

.ContentHeader__Title {
  font-size: 1.2em;
}

.ContentBody {
  padding: 1em;
  margin: auto;
  width: 100%;
}

@media only screen and (max-width: 755px) {
  .ContentBody {
    padding: 1em 0;
  }
}
